import React, { useEffect, useState } from "react";
import { Container, Form, Button } from "react-bootstrap";
import { collection, doc, getDoc, setDoc } from "firebase/firestore";

import db from "./firebase";

const Clipboard = () => {
	const [text, setText] = useState("");
	const [status, setStatus] = useState("");
	const [isLoading, setIsLoading] = useState(true);
	const [hasChanges, setHasChanges] = useState(false);

	const fetchData = async () => {
		setStatus("Loading...");
		setIsLoading(true);

		const docRef = doc(collection(db, "ea-main"), "ea-main");
		const docSnap = await getDoc(docRef);
		const currentTime = new Date().toLocaleString();

		if (docSnap.exists()) {
			console.log("doc exists");
			setText(docSnap.data().text);
			setStatus(`Loaded - ${currentTime}`);
		} else {
			setStatus("No data found...");
			console.log("No such doc");
		}

		setIsLoading(false);
	};

	useEffect(() => {
		const checkForChanges = async () => {
			const docRef = doc(collection(db, "ea-main"), "ea-main");
			const docSnap = await getDoc(docRef);

			if (docSnap.exists() && docSnap.data().text !== text) {
				setHasChanges(true);
			}
		};

		const interval = setInterval(() => {
			checkForChanges();
		}, 1 * 30 * 1000);

		return () => clearInterval(interval);
	}, [text]);

	useEffect(() => {
		fetchData();
	}, []);

	const handleChange = (e) => {
		if (!isLoading) {
			setText(e.target.value);
		}
	};

	const handleSave = async (textToSave) => {
		await setDoc(doc(collection(db, "ea-main"), "ea-main"), {
			text: text,
		});
		alert("Clipboard saved successfully");

		fetchData();
	};

	const handleCopy = () => {
		navigator.clipboard
			.writeText(text)
			.then(() => alert("Text copied to clipboard"))
			.catch((err) => console.error("Error copying text: ", err));
	};

	const handleRefresh = () => {
		fetchData();
		setHasChanges(false);
	};

	return (
		<div>
			<Form>
				<Container>
					<Form.Group
						className="mb-3 mt-4"
						controlId="exampleForm.ControlTextarea1"
					>
						<Form.Label className="text-xl">
							<h5>EA Notes</h5>
						</Form.Label>

						<div>
							<Form.Label>
								<h6>{isLoading ? "Loading..." : status}</h6>
							</Form.Label>
						</div>

						{hasChanges && (
							<div class="alert alert-danger" role="alert">
								<h3 className="">New changes!</h3>
								<Button
									variant="danger"
									onClick={handleRefresh}
									size="lg"
								>
									Refresh
								</Button>
							</div>
						)}

						<div>
							<Button
								variant="primary"
								onClick={handleSave}
								className="me-4"
								size="lg"
								disabled={hasChanges}
							>
								Save
							</Button>

							<Button
								variant="secondary"
								onClick={handleCopy}
								size="lg"
							>
								Copy
							</Button>
						</div>

						<Form.Control
							as="textarea"
							rows={30}
							value={text}
							onChange={handleChange}
							disabled={isLoading || hasChanges}
							className="mt-4"
						/>
					</Form.Group>
				</Container>
			</Form>
		</div>
	);
};

export default Clipboard;
