import { collection, getDoc, doc, getDocs, setDoc } from "firebase/firestore";
import React, { useEffect, useState } from "react";
import { Container, Form, Navbar, Button, ButtonGroup } from "react-bootstrap";
import db from "./firebase";

const Links = () => {
	const [links, setLinks] = useState([]);
	const [loading, setLoading] = useState(true);
	const [editIndex, setEditIndex] = useState(null);
	const [editedTitle, setEditedTitle] = useState("");
	const [editedURL, setEditedURL] = useState("");

	const fetchLinks = async () => {
		setLoading(true);

		const docRef = doc(db, "links", "links");
		const docSnap = await getDoc(docRef);

		if (docSnap.exists()) {
			const data = docSnap.data().links;

			setLinks(data);
		} else {
			console.log("No such document for links");
		}

		setLoading(false);
	};

	const handleEdit = (index) => {
		setEditIndex(index);
		setEditedTitle(links[index].title);
		setEditedURL(links[index].url);
	};

	const handleSave = async (index) => {
		const updatedLinks = [...links];
		updatedLinks[index] = { title: editedTitle, url: editedURL };

		await setDoc(doc(db, "links", "links"), { links: updatedLinks });

		setLinks(updatedLinks);
		setEditIndex(null);
	};

	const handleCancel = () => {
		setEditIndex(null);
	};

	useEffect(() => {
		fetchLinks();
	}, []);

	return (
		<div>
			<Navbar expand="lg" className="bg-body-tertiary">
				<Container>
					<Navbar.Brand className="mx-auto">Kumar EA's</Navbar.Brand>
				</Container>
			</Navbar>

			<Container className="mt-4">
				{loading ? (
					<h3>Loading...</h3>
				) : (
					links.map((link, index) => (
						<div key={index}>
							{editIndex === index ? (
								<div>
									<Form.Control
										type="text"
										value={editedTitle}
										onChange={(e) =>
											setEditedTitle(e.target.value)
										}
										className="mb-2"
										placeholder="Edit title"
									/>

									<Form.Control
										type="text"
										value={editedURL}
										onChange={(e) =>
											setEditedURL(e.target.value)
										}
										className="mb-2"
										placeholder="Edit URL"
									/>

									<div className="mt-2">
										<Button
											variant="success"
											onClick={() => handleSave(index)}
											className="me-2"
										>
											Save
										</Button>

										<Button
											variant="secondary"
											onClick={handleCancel}
										>
											Cancel
										</Button>
									</div>
								</div>
							) : (
								<div className="d-flex justify-content-between align-items-center">
									<h3>
										<a
											href={link.url}
											target="_blank"
											rel="noopener noreferrer"
										>
											{link.title}
										</a>
									</h3>
									<Button
										variant="warning"
										onClick={() => handleEdit(index)}
									>
										Edit
									</Button>
								</div>
							)}

							{/* <h3>
								<a href={link.url} target="_blank">
									{link.title}
								</a>
							</h3> */}
						</div>
					))
				)}

				<hr />
			</Container>
		</div>
	);
};

export default Links;
